.page-header {
  position: sticky;
  top: 0;
  z-index: 1;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 6px 3px -3px #00000017; }

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none; }
  .site-list__item a {
    font-family: "Info", "Arial", sans-serif;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    color: #000000; }
    .site-list__item a:hover {
      color: #ff5f0e;
      transition: all 400ms ease; }
    .site-list__item a:active {
      color: #7c7c7c; }

.social {
