  margin: 0;
  list-style-type: none; }

.sponsors__content-wrapper {
  padding-top: 100px; }

.sponsors__content h3 {
  margin: 0;
  font-family: "Info", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  text-transform: uppercase;
  text-align: center; }

.sponsors__visualization-wrapper {
  padding-top: 40px; }

.sponsors__visualization-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none; }

.sponsors__visualization-item:nth-child(n + 2) {
